.page-front {

    .intro {

        &.intro-temoignage {
            background: $color-temoignage;
            padding-bottom: 0;

            &:before {
                background: $color-temoignage;
            }

            .body-temoignage {
                padding-top: 4rem;
                background: #f1f1f1;
                margin: 0 -6rem;

                h1 {
                    padding: 0 6rem;
                    color: $color-temoignage;
                }

                p {
                    &:last-child {
                        margin-bottom: 4rem;

                    }

                }

                form {
                    padding: 2rem 6rem 12rem;
                }

            }

        }

        .dashboard-chiffres {

            > li {

                > p {
                    font-size: 2rem;
                    text-align: center;
                    margin-bottom: 1rem;
                }

                span {
                    display: block;
                    text-align: center;
                    font-size: 4.8rem;
                    font-weight: 100;
                    color: #fff;
                    margin-bottom: 5rem;

                    &:first-child {
                        margin-bottom: 2rem;
                    }

                }

                & + li {
                 border-top: 1px solid rgba(255, 255, 255, .2);
             }

             ul {
                display: inline-block;
                width: 100%;

                li {
                    float: left;
                    margin-left: 1rem;
                    @include calc (width, "33.3333333% - 1.5rem");
                    display: flex;
                    flex-flow: column wrap;
                    height: 151px;

                    p {
                        font-size: 1.4rem;
                        text-align: center;
                        margin-bottom: 1rem;
                        flex-grow: 1;
                    }

                    span {
                        font-size: 2.4rem;
                        flex-grow: 0;
                    }

                }

            }

        }

    }

}

.dashboard-temoignage {
    color: #000;

    >h2 {
        font-size: 1.8rem;
        color: $color-main;
        font-weight: 700;
        font-style: italic;
        text-transform: uppercase;
        text-align: left;
        padding: 0 2rem;

        @include media-breakpoint-up(md) {
            padding: 0 3rem;
        }

    }

    >p {
        color: $color-txt;
        padding: 0 2rem;
        max-width: 550px;

        @include media-breakpoint-up(md) {
            padding: 0 3rem;

        }

    }

    >a {
        font-size: 1.4rem;
        color: $color-main;
        padding: 1rem;
        margin-top: 3rem;
        display: inline-block;
        font-weight: 500;
        margin-right: 2rem;
        border: 1px solid $color-main;

        &.active {
            background: $color-main;
            color: #fff;
        }
    }

    .container-single-temoignage {
        display: flex;
        flex-flow: row wrap;
        margin: 0 0 2rem;
        background: rgba(248, 116, 68, .6);
        padding: 4rem 2rem;
        font-size: 1.6rem;
        color: #fff;

        @include media-breakpoint-up(md) {
            padding: 4rem 6rem;
            margin: 2rem 3rem;
        }

        @include media-breakpoint-up(lg) {
            padding: 4rem 6rem;
            margin: 4rem 6rem;
        }

        .header {
            width: 100%;
            text-align: left;

            h1 {
                display: inline-block;
                font-size: 3rem;
                font-style: italic;
                font-weight: 900;
                margin: 2.5rem 2rem 2rem 0;
            }

            .statut {

                img {
                    width: 20px;
                    margin-right: 1rem;

                }

            }

            .thumb-img-temoignage {
                width: 90px;
                height: 90px;
                border-radius: 50%;
                background-position: center;
                background-size: cover;
                background-repeat: no-repeat;
                display: inline-block;
                float: right;
            }

        }
        .body {

            h2 {
                font-size: 1.6rem;
                font-weight: 500;
                font-style: italic;
                text-align: left;
                color: #fff;
                text-transform: none;
                line-height: 1.6;
                max-width: 450px;
                margin-top: 2rem;
            }

            p {
                font-size: 1.4rem;
                color: #fff;
                line-height: 1.6;
            }

        }

        .footer {
            width: 100%;
            text-align: left;

            span {
                margin-bottom: 2rem;
                display: inline-block;
                float: right;
            }

        }

    }

    &.public-temoignage {
        .container-single-temoignage {
            background: rgba(117,177,165,0.8);
            padding: 4rem 0 0;


            .header,
            .body,
            .footer {
                padding: 0 2rem;

                @include media-breakpoint-up(md) {
                    padding: 0 6rem;
                }
            }

            a {
                @include datestyle;
                color: #fff;
                margin: 0 auto;
                padding: 3rem 0;
                display: block;
                width: 100%;
                background: $color-temoignage;
            }
        }

        >a {
            font-size: 1.4rem;
            color: $color-main;
            padding: 1rem;
            margin-top: 3rem;
            display: inline-block;
            font-weight: 500;
            margin-right: 2rem;
            border: 1px solid $color-temoignage;

            &.active {
                background: $color-temoignage;
                color: #fff;
            }
        }
    }
}

.links-temoignage {
    display: inline-block;
    float: right;
    width: 100%;
    text-align: right;
    border-top: 1px solid white;
    padding-top: 1rem;

    a {
        font-size: 1.2rem;
        font-weight: 700;
        text-transform: uppercase;
        position: relative;

        & + a {
            margin-left: 2rem;


            &:after {
                content: '';
                width: 1px;
                background: #fff;
                position: absolute;
                top: 0;
                left: -1rem;
                bottom: 0;
            }

        }

        &.link-back {
            float: left;
            font-size: 1.2rem;
            margin-left: 2rem;

            @include media-breakpoint-up(md) {
                margin-left: 5rem;
            }

        }

    }

    .white {
        color: #fff;

        &:hover {
            color: rgba($color-main, .75);
        }

    }

}

}
