    @include media-breakpoint-up(lg) {
        margin: 0 0 6rem;
    }

    &.piechart {
        width: 100%;
        max-width: 400px;
        // @include calc(width, "33.333333% - 2rem");
        height: auto;
        margin: 0 auto;
        display: inline-block !important;

    }

    .legend {
      list-style: none;
  }

